// export default http;
import axios from "axios";

// ====== è°ƒè¯•å¼€å…³ï¼ˆå¼€å‘å¼€ï¼Œç”Ÿäº§å…³ï¼‰ ======
const DEBUG_HTTP = true;

// åˆ›å»º Axios å®ä¾‹
const http = axios.create({
  baseURL: "http://localhost:8090", // âœ… ç½‘å…³åœ°å€
  timeout: 10000,
  withCredentials: true, // å¦‚æœåç«¯éœ€è¦ cookies æˆ– allowCredentials
});

// ================= è¯·æ±‚æ‹¦æˆªå™¨ =================
http.interceptors.request.use(
  config => {
    const token = localStorage.getItem("token");
    if (token) {
      config.headers.Authorization = `Bearer ${token.trim()}`;
    }

    if (DEBUG_HTTP) {
      console.groupCollapsed(
        `%câ¡ï¸ ${config.method?.toUpperCase()} ${config.url}`,
        "color:#67C23A;font-weight:bold"
      );
      console.log("Request config:", config);
      console.groupEnd();
    }

    return config;
  },
  error => Promise.reject(error)
);

// ================= å“åº”æ‹¦æˆªå™¨ =================
http.interceptors.response.use(
  response => {
    if (DEBUG_HTTP) {
      console.groupCollapsed(
        `%câ¬…ï¸ ${response.config.method?.toUpperCase()} ${response.config.url}`,
        "color:#409EFF;font-weight:bold"
      );
      console.log("Status:", response.status);
      console.log("Result:", response.data);
      console.groupEnd();
    }

    return response.data;
  },
  error => {
    if (DEBUG_HTTP) {
      console.groupCollapsed(
        `%câŒ HTTP ERROR ${error.config?.url}`,
        "color:red;font-weight:bold"
      );
      console.log("Error:", error);
      console.groupEnd();
    }

    if (error.response && error.response.status === 401) {
      alert("ç™»å½•å·²è¿‡æœŸæˆ–æœªç™»å½•ï¼Œè¯·é‡æ–°ç™»å½•");
      localStorage.removeItem("token");
      setTimeout(() => {
        window.location.href = "/login";
      }, 500);
    }

    return Promise.reject(error);
  }
);

export default http;
