<template>
  <div :style="bgStyle">
    <div class="page-container">
      <!-- é¡¶éƒ¨æ ‡é¢˜å’Œäº§å“ä¿¡æ¯ -->
      <el-card class="header-card" shadow="never">
        <h1 class="main-title">{{ product?.name || "åŠ è½½ä¸­..." }}</h1>
        <div class="product-info" v-if="product">
          <p>
            ç±»å‹ï¼š<el-tag>{{ product.type }}</el-tag>
          </p>
          <p>ä½œè€…/å¼€å‘å•†ï¼š{{ product.author || "æœªçŸ¥" }}</p>
          <p>é›†æ•°/ç« èŠ‚/å·æ•°ï¼š{{ product.countDisplay || "æš‚æ— " }}</p>
        </div>
      </el-card>

      <!-- èµ„æºåˆ—è¡¨ -->
      <div class="resource-list">
        <el-card
          v-for="res in resources"
          :key="res.id"
          class="resource-card"
          shadow="hover"
        >
          <div v-if="res.hasResource" class="resource-info">
            <p>èµ„æºåç§°ï¼š{{ res.name }}</p>
            <p>æè¿°ï¼š{{ res.description || "æš‚æ— æè¿°" }}</p>
            <el-button
              type="success"
              size="mini"
              @click="downloadResource(res.resourcePath, res.name)"
            >
              ä¸‹è½½
            </el-button>
          </div>
          <div v-else class="no-resource">æš‚æ— èµ„æº</div>
        </el-card>

        <!-- ä¸Šä¼ èµ„æºæŒ‰é’® -->
        <el-button
          type="primary"
          icon="el-icon-upload"
          class="upload-btn"
          @click="openUploadDialog"
        >
          ä¸Šä¼ èµ„æº
        </el-button>
      </div>

      <!-- ä¸Šä¼ èµ„æºå¼¹çª— -->
      <el-dialog
        title="ä¸Šä¼ èµ„æº"
        :visible.sync="uploadDialogVisible"
        width="500px"
      >
        <el-form :model="uploadForm" label-width="100px">
          <el-form-item label="èµ„æºåç§°" required>
            <el-input v-model="uploadForm.name"></el-input>
          </el-form-item>

          <el-form-item label="èµ„æºæè¿°">
            <el-input
              type="textarea"
              v-model="uploadForm.description"
              rows="3"
            ></el-input>
          </el-form-item>

          <el-form-item label="èµ„æºæ–‡ä»¶" required>
            <el-upload
              :action="fakeAction"
              :before-upload="beforeUpload"
              :show-file-list="false"
            >
              <el-button>é€‰æ‹©æ–‡ä»¶</el-button>
            </el-upload>
            <span v-if="uploadForm.file">{{ uploadForm.file.name }}</span>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="uploadDialogVisible = false">å–æ¶ˆ</el-button>
          <el-button type="primary" @click="submitUpload">ç¡®å®šä¸Šä¼ </el-button>
        </span>
      </el-dialog>

      <!-- æ‰“åˆ†åŒº -->
      <el-card class="rating-card common-card" shadow="hover">
        <p>æˆ‘çš„è¯„åˆ†ï¼š</p>
        <el-rate v-model="userScore" :max="10" show-score></el-rate>
        <div class="btn-container">
          <el-button type="primary" @click="submitScore">æäº¤è¯„åˆ†</el-button>
        </div>
      </el-card>

      <!-- è¯„è®ºåŒº -->
      <el-card class="comment-card common-card" shadow="hover">
        <el-input
          type="textarea"
          v-model="newComment"
          placeholder="å†™ä¸‹ä½ çš„è¯„è®º..."
          rows="3"
        ></el-input>
        <div class="btn-container">
          <el-button type="primary" @click="submitComment">å‘é€</el-button>
        </div>
      </el-card>

      <!-- è¯„è®ºå±•ç¤º -->
      <div class="comment-list">
        <el-card
          v-for="c in comments"
          :key="c.id"
          class="comment-item"
          shadow="hover"
        >
          <p class="comment-username">{{ c.username }}ï¼š</p>
          <p class="comment-content">{{ c.content }}</p>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
import bgImage from "@/assets/bg.jpg";
import http from "@/utils/http";
import { getUserIdFromToken } from "@/utils/auth";

export default {
  props: ["id"],
  data() {
    return {
      bgStyle: {
        backgroundImage: `url(${bgImage})`,
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center center",
        backgroundSize: "cover",
        minHeight: "100vh",
        padding: "40px 20px",
      },
      product: null,
      resources: [],
      userScore: 0,
      newComment: "",
      comments: [],
      loading: false,

      uploadDialogVisible: false,
      uploadForm: { file: null, name: "", description: "" },
      fakeAction: "http://localhost/",
      gatewayUrl: "http://localhost:8090",
    };
  },
  mounted() {
    const id = this.$route.params.id;
    if (id) {
      this.loadProductDetail(id);
      this.loadResources(id);
      this.loadComments(id);
    }
  },
  methods: {
    async loadProductDetail(id) {
      this.loading = true;
      try {
        const res = await http.get(`/acg-product/${id}`);
        if (res.code === 1) {
          const p = res.data;
          const typeMap = {
            ANIME: "åŠ¨æ¼«",
            COMIC: "æ¼«ç”»",
            NOVEL: "è½»å°è¯´",
            GAME: "æ¸¸æˆ",
          };
          let countDisplay = "";
          if (p.episodeCount) countDisplay = `${p.episodeCount}é›†`;
          else if (p.chapterCount) countDisplay = `${p.chapterCount}ç« `;
          else if (p.volumeCount) countDisplay = `${p.volumeCount}å·`;

          this.product = {
            name: p.name,
            type: typeMap[p.type] || p.type,
            author: p.author || p.developer || p.studio || "æœªçŸ¥",
            countDisplay: countDisplay || "æš‚æ— ",
            cover: p.coverPath ? `${p.coverPath}` : "",
            totalScore: p.totalScore || 0,
          };
        } else this.$message.error(res.msg || "è·å–äº§å“ä¿¡æ¯å¤±è´¥");
      } catch (e) {
        console.error(e);
        this.$message.error("è·å–äº§å“ä¿¡æ¯å¤±è´¥");
      } finally {
        this.loading = false;
      }
    },
    async loadResources(id) {
      try {
        const res = await http.get(`/acg-resource/list/${id}`);
        if (res.code === 1) {
          this.resources = res.data.map((r) => ({
            id: r.id,
            hasResource: !!r.resourcePath,
            name: r.name,
            description: r.description,
            resourcePath: r.resourcePath,
          }));
        } else this.$message.error(res.msg || "è·å–èµ„æºå¤±è´¥");
      } catch (e) {
        console.error(e);
        this.$message.error("è·å–èµ„æºå¤±è´¥");
      }
    },
    // downloadResource(path, name) {
    //   if (!path) return;
    //   const url = `${this.gatewayUrl}/${path}`;
    //   const link = document.createElement("a");
    //   link.href = url;
    //   link.download = name || "èµ„æºæ–‡ä»¶";
    //   document.body.appendChild(link);
    //   link.click();
    //   document.body.removeChild(link);
    // },

    downloadResource(path, name) {
      if (!path) return;

      // ä¿è¯ path ä»¥ / å¼€å¤´ï¼ˆåç«¯è¿”å›ç»å¯¹ Web è·¯å¾„ï¼‰
      const normalizedPath = path.startsWith("/") ? path : `/${path}`;

      // ç½‘å…³ + Web èµ„æºè·¯å¾„
      const url = `${this.gatewayUrl}${normalizedPath}`;

      const link = document.createElement("a");
      link.href = url;
      link.download = name || "èµ„æºæ–‡ä»¶";
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },

    openUploadDialog() {
      this.uploadDialogVisible = true;
      this.uploadForm = { file: null, name: "", description: "" };
    },
    beforeUpload(file) {
      this.uploadForm.file = file;
      return false;
    },
    async submitUpload() {
      const userId = getUserIdFromToken();
      if (!userId) {
        this.$message.error("è¯·å…ˆç™»å½•");
        return;
      }
      if (!this.uploadForm.file || !this.uploadForm.name) {
        this.$message.error("è¯·å¡«å†™èµ„æºåç§°å¹¶é€‰æ‹©æ–‡ä»¶");
        return;
      }

      const formData = new FormData();
      formData.append("file", this.uploadForm.file);
      formData.append("userId", userId);
      formData.append("name", this.uploadForm.name);
      formData.append("description", this.uploadForm.description || "");
      formData.append("productId", this.$route.params.id);

      try {
        const res = await http.post("/acg-resource/upload", formData, {
          headers: { "Content-Type": "multipart/form-data" },
        });

        if (res.code === 1 || res.data?.id) {
          this.$message.success("ä¸Šä¼ æˆåŠŸ");
          this.uploadDialogVisible = false;
          this.uploadForm = { file: null, name: "", description: "" };
          this.loadResources(this.$route.params.id);
        } else this.$message.error(res.msg || "ä¸Šä¼ å¤±è´¥");
      } catch (e) {
        console.error(e);
        this.$message.error("ä¸Šä¼ å¤±è´¥");
      }
    },
    async submitScore() {
      const userId = getUserIdFromToken();
      if (!userId) {
        this.$message.error("è¯·å…ˆç™»å½•");
        return;
      }

      try {
        const res = await http.post("/acg-rating/rate", null, {
          params: {
            userId,
            productId: this.$route.params.id,
            score: this.userScore,
          },
        });

        if (res.code === 1) this.$message.success("è¯„åˆ†æˆåŠŸ");
        else this.$message.error(res.msg || "è¯„åˆ†å¤±è´¥");
      } catch (e) {
        console.error(e);
        this.$message.error("è¯„åˆ†å¤±è´¥");
      }
    },
    async loadComments(productId) {
      try {
        const res = await http.get(`/acg-comment/list/${productId}`);
        if (res.code === 1) {
          const commentsWithUser = await Promise.all(
            res.data.map(async (c) => {
              if (c.userId) {
                try {
                  const userRes = await http.get(`/user/${c.userId}`);
                  return {
                    id: c.id,
                    username:
                      userRes.code === 1 && userRes.data
                        ? userRes.data.userName
                        : "æœªçŸ¥ç”¨æˆ·",
                    content: c.content,
                  };
                } catch (e) {
                  console.error(e);
                  return { id: c.id, username: "æœªçŸ¥ç”¨æˆ·", content: c.content };
                }
              } else
                return { id: c.id, username: "æœªçŸ¥ç”¨æˆ·", content: c.content };
            })
          );

          this.comments = commentsWithUser;
        } else this.$message.error(res.msg || "è·å–è¯„è®ºå¤±è´¥");
      } catch (e) {
        console.error(e);
        this.$message.error("è·å–è¯„è®ºå¤±è´¥");
      }
    },
    async submitComment() {
      const userId = getUserIdFromToken();
      if (!userId) {
        this.$message.error("è¯·å…ˆç™»å½•");
        return;
      }
      if (!this.newComment) return;

      try {
        const res = await http.post("/acg-comment/add", null, {
          params: {
            userId,
            productId: this.$route.params.id,
            content: this.newComment,
          },
        });

        if (res.code === 1) {
          this.newComment = "";
          this.$message.success("è¯„è®ºæˆåŠŸ");
          this.loadComments(this.$route.params.id);
        } else this.$message.error(res.msg || "è¯„è®ºå¤±è´¥");
      } catch (e) {
        console.error(e);
        this.$message.error("è¯„è®ºå¤±è´¥");
      }
    },
  },
};
</script>

<style scoped>
