.btn-container {
  margin-top: 20px; /* æŒ‰é’®ä¸‹ç§» */
  display: flex;
  justify-content: center; /* æŒ‰é’®å±…ä¸­ */
}

.comment-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
}

.comment-item {
  width: 80%;
  padding: 10px;
}

.comment-username {
  font-weight: bold;
  color: #ffdd55;
  text-align: left;
}

.comment-content {
  color: #000;
